.toggle-modal {
  position: fixed;
  z-index: 999;
  display: none;
  width: 100%;
  max-width: 560px;
  top: 50%;
  left: 50%;
  animation-duration: 0.7s;
  transition: 0.3s ease-in-out;

  @media (max-width: 600px) {
    padding: 0 15px;
  }

  &__middle {
    max-width: 560px;
  }

  &.active {
    display: block;
    animation: popupShow 0.3s cubic-bezier(0.13, 0.3, 0.17, 0.99);
    animation-fill-mode: both;

    @media screen and (max-width: 1023.98px) {
      animation-duration: 0.3s;
      animation-timing-function: ease-in-out;
    }

    .rating-area:not(:checked) {
      > label {
        animation-name: ratingAreaShow;
        animation-duration: 0.7s;
        animation-timing-function: cubic-bezier(0.13, 0.3, 0.17, 0.99);
      }
    }

  }

  &.remove {
    animation: popupHide 0.3s cubic-bezier(0.13, 0.3, 0.17, 0.99);
    animation-fill-mode: both;

    @media screen and (max-width: 1023.98px) {
      animation: popupHide 0.3s ease-in-out;
      animation-fill-mode: both;
    }

    .rating-area:not(:checked) {
      > label {
        animation-name: ratingAreaHide;
        animation-duration: 0.7s;
        animation-timing-function: cubic-bezier(0.13, 0.3, 0.17, 0.99);
      }
    }
  }

  &__box {
    position: relative;
    overflow: hidden;
    z-index: 1;
    padding: 20px 45px 30px;
    box-shadow: 0 28px 59px @black-rgba-two,
    0 40px 120px @black-rgba-five;
    height: 100%;
    border-radius: 25px;
    transform: translateZ(0);

    &_p-none {
      padding: 0;
    }
  }

  &__text {
    margin: 0 0 22px;
    text-align: center;
    width: 100%;
    font-size: 22px;
    font-weight: 500;

    @media (max-width: @tablet-width) {
      font-size: 16px;
      margin-bottom: 10px;
    }
  }

  &__buttons {
    width: 240px;
    margin: 0 auto;

    & .button {
      height: 53px;
      padding-top: 0;
      padding-bottom: 0;
      margin-bottom: 20px;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  &__close {
    padding: 0;
    position: absolute;
    display: block;
    z-index: 2;
    width: 50px;
    height: 50px;
    top: -15px;
    right: -15px;
    border-radius: 50%;
    border: none;
    background-color: @grey-two;
    box-sizing: border-box;
    transition: 0.5s;
    cursor: pointer;
    transform: rotate(0deg);

    @media (max-width: @tablet-width) {
      width: 40px;
      height: 40px;
    }
    @media screen and (max-width: 600px) {
      right: 7px;
    }

    @media screen and (max-width: 450.98px) {
      top: -10px;
    }

    @media screen and (max-width: 380px) {
      top: 5px;
      right: 17px;
      border-radius: 0;
      background-color: transparent;
    }

    //@media screen and (max-width: 1024px) and (orientation: landscape) {
    //  top: 5px;
    //  right: 5px;
    //  border: none;
    //  background-color: transparent;
    //}
    @media screen and (max-width: 854px) and (orientation: landscape) {
      top: 0;
    }

    @media screen and (max-width: 600px) and (orientation: landscape) {
      top: 5px;
      right: 17px;
      border: none;
      background-color: transparent;
    }

    &:hover {
      @media screen and (min-width: 451px) {
        transform: rotate(180deg);
        background-color: @color-red;
      }

      &::before {
        transform: translate(-50%, -50%) rotate(135deg);
      }

      &::after {
        transform: translate(-50%, -50%) rotate(-135deg);
      }
    }

    &:focus {
      @media screen and (min-width: 451px) {
        background-color: @color-red;
      }
    }

    &::before,
    &::after {
      content: "";
      position: absolute;
      display: block;
      width: 28px;
      height: 2px;
      top: 50%;
      left: 50%;
      background-color: @white;
      transition: 0.5s;
      border-radius: 30px;
      @media (max-width: @tablet-width) {
        width: 25px;
        height: 2px;
      }
    }

    &::before {
      transform: translate(-50%, -50%) rotate(45deg);
    }

    &::after {
      transform: translate(-50%, -50%) rotate(-45deg);
    }

    &_sm-green {
      &::before,
      &::after {
        @media screen and (max-width: 380px) {
          background-color: @color-green;
        }
        @media screen and (max-width: 1024px) and (orientation: landscape) {
          background-color: @color-green;
        }
        @media screen and (min-width: 450px) and (max-height: 450px) {
          background-color: @color-green;
        }
      }

      &:hover {
        @media screen and (max-width: 380px) {
          background-color: transparent;
        }

        &::before {
          @media screen and (max-width: 380px) {
            background-color: @color-red;
          }
          @media screen and (min-width: 450px) and (max-height: 450px) {
            background-color: @color-red;
          }
        }

        &::after {
          @media screen and (max-width: 380px) {
            background-color: @color-red;
          }
          @media screen and (min-width: 450px) and (max-height: 450px) {
            background-color: @color-red;
          }
        }
      }

      &:focus {
        @media screen and (max-width: 380px) {
          background-color: transparent;
          @media screen and (min-width: 450px) and (max-height: 450px) {
            background-color: transparent;
          }
        }
      }
    }
  }

  &__header {
    padding: 21px 30px;
    text-align: center;
    background-color: @color-green;
    @media screen and (max-width: 1024px) and (orientation: landscape) {
      padding: 15px 30px;
    }

    @media screen and (max-width: 450.98px) {
      padding-top: 15px;
      padding-bottom: 15px;
    }

    @media screen and (max-width: 400px) {
      padding-left: 20px;
      padding-right: 20px;
    }

    figure {
      margin: 0;

      svg {
        width: 50px;
        height: 50px;
        margin-bottom: 14px;
        transition: 0.3s ease-in-out;
        @media screen and (max-width: 450.98px) {
          margin-bottom: 5px;
          padding: 0;
        }
        @media screen and (max-width: 575px) and (max-height: 451px) {
          width: 55px;
          height: 55px;
        }
        @media screen and (min-width: 576px) {
          width: 80px;
          height: 80px;
        }
        @media screen and (max-width: 1024px) and (orientation: landscape) {
          width: 55px;
          height: 55px;
        }
        @media screen and (max-width: 854px) and (orientation: landscape) {
          display: none;
        }
      }

      img {
        width: auto;
        height: 50px;
        margin-bottom: 14px;
        transition: 0.3s ease-in-out;
        @media screen and (max-width: 450.98px) {
          margin-bottom: 5px;
          padding: 0;
        }
        @media screen and (max-width: 575px) and (max-height: 451px) {
          height: 55px;
        }
        @media screen and (min-width: 576px) {
          height: 80px;
        }
        @media screen and (max-width: 1024px) and (orientation: landscape) {
          height: 55px;
        }
        @media screen and (max-width: 854px) and (orientation: landscape) {
          display: none;
        }
      }

      figcaption {
        font-weight: 500;
        font-size: 22px;
        line-height: 1.5em;
        color: @white;
        transition: 0.3s ease-in-out;
        @media screen and (max-width: 991.98px) {
          font-size: 18px;
        }
        @media screen and (max-width: 900px) {
          font-size: 16px;
        }

        @media screen and (max-width: 854px) and (orientation: landscape) {
          text-transform: uppercase;
        }
      }
    }
  }

  &__body {
    position: relative;
    padding: 30px 30px;
    @media screen and (max-width: 1024px) and (orientation: landscape) {
      padding: 30px 30px;
    }

    @media screen and (max-width: 450.98px) {
      padding: 30px 20px;
    }

    @media screen and (max-width: 400px) {
      padding-left: 20px;
      padding-right: 20px;
    }

    &_white {
      background-color: @white;
      @media screen and (max-width: 380px) {
        padding-top: 20px;
      }
    }

    &_pb-50 {
      padding-bottom: 50px;
      @media screen and (max-width: 1024px) and (orientation: landscape) {
        padding-bottom: 30px;
      }
      @media screen and (max-width: 854px) and (orientation: landscape) {
        padding-bottom: 15px;
      }
      @media screen and (max-width: 450.98px) {
        padding-bottom: 30px;
      }
      @media screen and (max-width: 380px) {
        padding-bottom: 20px;
      }
    }

    &_pb-35 {
      padding-bottom: 35px;
      @media screen and (max-width: 1024px) and (orientation: landscape) {
        padding-bottom: 30px;
      }
      @media screen and (max-width: 854px) and (orientation: landscape) {
        padding-bottom: 15px;
      }
      @media screen and (max-width: 450.98px) {
        padding-bottom: 30px;
      }
      @media screen and (max-width: 380px) {
        padding-bottom: 20px;
      }
    }

    &_pt-sm-50 {
      @media screen and (max-width: 380px) {
        padding-top: 50px;
      }
    }
  }

  &__link {
    padding: 0;
    border: none;
    outline: none;
    font-size: 15px;
    font-weight: 500;
    text-decoration: underline;
    color: @color-green;
    transition: 0.2s;
    cursor: pointer;
    background: none;

    &:hover {
      text-decoration: none;
    }
  }

  &__successfully {
    p {
      &:first-child {
        margin-bottom: 0;
      }
    }

    & .button {
      margin: auto;
    }
  }

  & .attach {
    &__item {
      width: 100%;
      margin: 0;

      & label, & div.attach__button {
        padding-bottom: 0;
      }
    }

    & .button__little {
      padding-left: 12px;
      padding-right: 10px;
      min-width: 220px;
      @media screen and (max-width: 400px) {
        min-width: 100%;
        max-width: 100%;
        min-height: 40px;
      }

      & img {
        width: max-content;
        height: 29px;
        margin-right: 5px;
        @media screen and (min-width: 400.98px) {
          height: 40px;
        }
      }

      &.js-open-modal-success-unsuccess {
        & svg {
          width: max-content;
          height: 21px;
          margin-top: 2px;
          margin-right: 10px;
          @media screen and (min-width: 400.98px) {
            height: 29px;
          }
        }
      }
    }

    & ~ .button {
      margin-top: 20px;
    }
  }

  & .button__small {
    @media screen and (max-width: 400px) {
      min-width: 100%;
      max-width: 100%;
    }
  }

  /**** Modifier ****/

  &__text {
    &_uppercase {
      text-transform: uppercase;
    }
  }

  &__default-text {
    font-size: 14px;
    line-height: 1.5em;
    @media only screen and (min-width: 900px) {
      font-size: 16px;
    }
    @media only screen and (min-width: 992px) {
      font-size: 18px;
    }

    p {
      margin-top: 0;
      margin-bottom: 10px;
    }

    & .modal__buttons {
      margin-top: 30px;
    }
  }

  &[data-modal="messageForClientPTF"] {
    @media screen and (max-width: 575.98px) and (max-height: 451px), screen and (max-width: 640px) and (max-height: 480px), screen and (max-width: 520px) and (max-height: 680px) {
      top: 0;
      position: absolute;
      margin-top: 15px;
    }

    &.active {
      @media screen and (max-width: 575.98px) and (max-height: 451px), screen and (max-width: 640px) and (max-height: 480px), screen and (max-width: 520px) and (max-height: 680px) {
        display: block;
        animation: popupWrapperShow 0.4s cubic-bezier(0.13, 0.3, 0.17, 0.99);
        animation-fill-mode: both;
        transform: translateX(-50%);
      }

      @media screen and (max-width: 1023.98px) {
        animation-duration: 0.3s;
        animation-timing-function: ease-in-out;
      }
    }

    &.remove {
      @media screen and (max-width: 575.98px) and (max-height: 451px), screen and (max-width: 640px) and (max-height: 480px), screen and (max-width: 520px) and (max-height: 680px) {
        animation: popupWrapperHide 0.4s cubic-bezier(0.13, 0.3, 0.17, 0.99);
      }

      @media screen and (max-width: 1023.98px) {
        animation-duration: 0.3s;
        animation-timing-function: ease-in-out;
      }
    }
  }

  &.errorSend {
    & .modal__text {
      color: @color-red;
    }
  }
}

.toggle-modal-bg {
  position: fixed;
  z-index: 998;
  display: none;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: @black-rgba-three;
  overflow: hidden;

  &.active {
    display: flex;
    animation: popupWrapperShow 0.4s cubic-bezier(0.13, 0.3, 0.17, 0.99);
  }

  &.remove {
    animation: popupWrapperHide 0.4s cubic-bezier(0.13, 0.3, 0.17, 0.99);
    animation-fill-mode: both;
  }
}
